    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
  display: flex;
  justify-content: center;
  align-items: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
h1 {
  margin: 10px;
  color: green;
  font-style: italic;
}
li {
  list-style: none;
  color: grey;
  padding-top: 5px;
  font-size: 15px;
}
.note {
  color: black;
  font-size: 20px;
}
p {
  padding: 0 0 0 10px;
  margin: 2px 0 2px 10px;
}
button {
  margin: 10px;
}
table {
  margin: 10px;
}
